import Landing from './components/Landing'

const Dashboard = resolve => {
  require.ensure(['./components/panels/Dashboard.vue'], () => {
    resolve(require('./components/panels/Dashboard.vue'))
  })
}

const AddInfo = resolve => {
  require.ensure(['./components/panels/AddInfo.vue'], () => {
    resolve(require('./components/panels/AddInfo.vue'))
  })
}

export const routes = [
  { path: '/', component: Landing },
  { path: '/edit', component: AddInfo },
  { path: '/home', component: Dashboard },
  { path: '*', redirect: '/home'}
]
